<template>
    <div>
        <el-table :data="messages" border stripe>
            <el-table-column type="index"></el-table-column>
            <el-table-column label="" prop="nickname" width="180px"></el-table-column>
            <el-table-column label="" prop="email" width="200px"></el-table-column>
            <el-table-column label="IP" prop="ip" width="180px"></el-table-column>
            <el-table-column label="" width="200px">
                <template slot-scope="scope">
                    <i class="el-icon-time"></i>
                    <span style="margin-left: 10px">{{ dateFormat(scope.row.createTime) }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" prop="content"></el-table-column>
            <el-table-column label="" width="100px">
                <template slot-scope="scope">
                    <span style="margin-left: 10px">{{ statusStr(scope.row.status)}}</span>
                </template>
            </el-table-column>
            <el-table-column label=""  width="150">
                <template slot-scope="scope">
                    <el-button size="mini" :disabled="scope.row.status === 1" @click="handleStatus(scope.row.id, 1)"></el-button>
                    <el-button size="mini" :disabled="scope.row.status === 2" type="danger" @click="handleStatus(scope.row.id, 2)"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--  -->
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                       :current-page="queryInfo.pageNum" :page-sizes="[10, 12, 15, 20]" :page-size="queryInfo.pageSize"
                       layout="total, sizes, prev, pager, next, jumper" :total="total">
        </el-pagination>
    </div>
</template>

<script>

import dayjs from "dayjs";

export default {
    name: "ManageMessage",
    data() {
        return {
            messages: [],
            total: 0,
            queryInfo: {
                pageNum: 1,
                pageSize: 10
            }
        }
    },
    methods: {
        async getMessageList() {
            const {data:res} = await this.$axios.get("/admin/msgList", {params: this.queryInfo});
            console.log(res)
            if(res.data) {
                this.messages.splice(0, this.messages.length)
                this.messages.push(...res.data)
                this.total = res.count
            }
        },
        async handleStatus(id, status) {
            const {data:res} = await this.$axios.put("/admin/updateMsgStatus", {id: id, status: status});
            if (res.status !== 101) {
                status === 1 ? this.$message.error("") : this.$message.error("")
            } else {
                status === 1 ? this.$message.success("") : this.$message.error("")
                await this.getMessageList()
            }
        },
        handleSizeChange: function(pagesize) {     // pagesize 
            this.queryInfo.pageSize = pagesize;
            this.getMessageList();
        },
        handleCurrentChange: function(newPage) {  // 
            this.queryInfo.pageNum = newPage;
            this.getMessageList();
        },
        dateFormat(d) {
            return dayjs(d).format("YYYY-MM-DD HH:mm:ss")
        },
        statusStr(status) {
            if (status === 0) {
                return ""
            } else if (status === 1) {
                return ""
            } else {
                return ""
            }
        }
    },
    created() {
        this.getMessageList()
    }
}
</script>

