
<template>

    <div class="login_container ">
        <div class="login_box">
            <div class="login_font"></div>
            <!--  -->
            <el-form ref="loginFormRef" :model="loginForm" :rules="loginFormRules" label-width="0px" class="login_form">
                <!--  -->
                <el-form-item prop="username">
                    <el-input v-model="loginForm.username" prefix-icon="el-icon-user-solid" placeholder=""></el-input>
                </el-form-item>
                <!--  -->
                <el-form-item prop="password">
                    <el-input v-model="loginForm.password" @keyup.enter.native="login" prefix-icon="el-icon-lock" type="password" placeholder=""></el-input>
                </el-form-item>
                <!--  -->
                <el-form-item class="btns">
                    <el-button type="primary" @click="login"></el-button>
                    <el-button type="primary" @click="resetLoginForm"></el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>

</template>



<script>

import crypot from 'crypto'

export default {
    data() {
        return {
            // 
            loginForm: {
                username: "",
                password: ""
            },
            //
            loginFormRules: {
                // 
                username: [
                    { required: true, message: "", trigger: "blur"},
                    { min: 3, max: 11, message: " 3  10 ", trigger: "blur"}
                ],
                // 
                password: [
                    { required: true, message: "", trigger: "blur"},
                    { min: 6, max: 15, message: " 6  15 ", trigger: "blur"}
                ]
            }
        }
    },
    methods: {
        resetLoginForm() {
            this.$refs.loginFormRef.resetFields();
        },
        login() {   //
            this.$refs.loginFormRef.validate(async (valid) =>{
                if(!valid) return;     //
                let md5 = crypot.createHash('md5');
                md5.update(this.loginForm.password);
                const passwd = md5.digest('hex')
                const forms = {
                    username: this.loginForm.username,
                    password: passwd
                }

                const {data: res} = await this.$axios.post("/admin/login", forms);
                if(res.status == 200) {   //
                    return this.$message.error(res.message);
                }

                this.$message.success(res.message);
                // :
                    //1 TokensessionStoragetoken
                    //    TokensessionStorage
                    // sessionStorage  localStorage
                    //2 .home
                window.sessionStorage.setItem("token", res.token);
                window.sessionStorage.setItem("userId", res.id);
                await this.$router.push("/manage/home")
            });
        }
    }
}
</script>



<style lang="less" scoped>

.login_container {
    background: url("~@/assets/images/back7.jpg");
    width:100%;
    height: 100%;
    background-size:100% 100%;
}

.login_box {
    width: 450px;
    height: 300px;
    background-color: rgba(255, 255, 255, .3);
    border-radius: 3px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -60%);
}

.login_form {
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;
}

.el-input {
    opacity: 0.5;
}

.btns {
    display: flex;
    justify-content: flex-end;
}

.login_font {
    font-size: 30px;
    font-weight: bold;
    color: #00B5AD;
    width: 100%;
    padding-left: 130px;
    padding-top: 30px;
}
</style>
