
<template>

    <div width="100%">
        <el-card class="box-card">
            <el-row :gutter="10">
                <el-col :span="2">
                    <el-select :value="postInfo.flag" @change="changeWriteType" clearable>
                        <el-option v-for="item in writeTypes" :key="item.id" :value="item.type">
                        </el-option>
                    </el-select>
                </el-col>
                <el-col :span="4">
                    <el-select :value="selectedType" @visible-change="getAllTypes" @change="changeType" clearable placeholder="">
                        <el-option v-for="item in types" :key="item.id" :value="item.name">
                        </el-option>
                    </el-select>
                </el-col>
                <el-col :span="16">
                    <el-input v-model="postInfo.title" placeholder="" clearable></el-input>
                </el-col>
            </el-row>

            <el-row class="rowDown" :gutter="10">
                <el-col :span="6">
                    <el-select class="tag_select" v-model="selectedTag" @visible-change="getAllTags" clearable multiple filterable default-first-option placeholder="">
                        <el-option v-for="item in tags" :label="item.name" :key="item.id" :value="item.name">
                        </el-option>
                    </el-select>
                </el-col>
                <el-col :span="14">
                    <el-input v-model="postInfo.firstPicture" placeholder="" clearable></el-input>
                </el-col>
                <el-col :span="2">
                    <el-upload :on-success="uploadSuccess" :show-file-list="false" class="upload-demo" :action="uploadFirstPic">
                        <el-button  type="primary" plain></el-button>
                        <div slot="tip" class="el-upload__tip">jpg/png</div>
                    </el-upload>
                </el-col>
            </el-row>

            <!-- markdown -->
            <mavon-editor style="min-height:600px;font-size:18px" fontSize="16px" class="markdown"
            :ishljs="true" :scrollStyle="true" v-model="postInfo.content" ref=md @imgAdd="$imgAdd"/>

            <!--  -->
            <el-input class="textarea" type="textarea" :rows="5" placeholder="..." v-model="postInfo.description">
            </el-input>

            <el-checkbox v-model="postInfo.appreciation" label=""></el-checkbox>
            <el-checkbox v-model="postInfo.commentabled" label=""></el-checkbox>
            <el-checkbox v-model="postInfo.recommend" label=""></el-checkbox>
            <el-checkbox v-model="postInfo.shareStatement" label=""></el-checkbox>

            <el-row class="endbtns" type="flex" justify="end">
                <el-button type="primary" plain @click="saveBlog(false)"></el-button>
                <el-button type="success" plain @click="publishBlog"></el-button>
            </el-row>
        </el-card>
    </div>

</template>



<script>
import axios from "axios";

export default {
    data() {
        return {
            markdownText: "",
            writeTypes: [
                {id: 1, type: ""},
                {id: 2, type: ""},
                {id: 3, type: ""}
            ],
            types: [],
            selectedType: "",
            tags: [],
            selectedTag: [],
            postInfo: {
                id: 0,
                userId: 0,
                title: "",
                firstPicture: "",
                flag: "",           //||
                typeId: 0,
                published: false,
                content: "",
                commentabled: false,       //
                appreciation: false,       //
                recommend: false,          //
                shareStatement: false,    //
                description: "",
                tagIds: [],
            },
            uploadFirstPic: axios.defaults.baseURL + "/admin/uploadImages"
        }
    },
    created() {
        this.checkEditOrAdd();
        this.getAllTypes(true)
        this.getAllTags(true)
    },
    methods: {
        changeWriteType: function(name) {
            this.postInfo.flag = name;
        },
        getAllTypes: async function(change) {
            if(change === true) {    //true false
                const{data: res} = await this.$axios.get("/admin/getAllTypes");
                if(res.status === 1) {
                    this.types = res.data;
                } else {
                    this.$message.error(res.message);
                }
            }
        },
        changeType: function(name) {
            this.selectedType = name;
            var item = this.types.find(item => {
                return item.name === name
            })
            this.postInfo.typeId = item.id;
        },
        getAllTags: async function(change) {
            if(change == true) {    //true false
                const{data: res} = await this.$axios.get("/admin/getAllTags");
                if(res.status == 1) {
                    this.tags = res.data;
                } else {
                    this.$message.error(res.message);
                }
            }
        },

        checkEditOrAdd: async function() {      //
            const blogId = this.$route.query.id
            if(typeof blogId === "undefined") {
                this.postInfo.id = null
                return
            }

            this.postInfo.id = blogId;
            const {data: res} = await this.$axios.get("/admin/getFullBlog", {params: {id: blogId}});
            if(!res.status) {
                this.$message.error("");
                await this.$router.push("/listBlogs");
            }
            const blog = res.data;

            this.postInfo = { ...this.postInfo, ...res.data}
            this.postInfo.tagIds = res.tags.map(x => {return x.id});
            this.markdownText = this.postInfo.content;
            this.selectedType = blog.typename;
            this.selectedTag = res.tags.map(x => {return x.name});
        },
        setPostInfo: function() {
            const userId = window.sessionStorage.getItem("userId");
            this.postInfo.userId = parseInt(userId);
            for (let i = 0; i < this.selectedTag.length; i++) {
                this.postInfo.tagIds[i] = this.tags.find(item => {return item.name === this.selectedTag[i]}).id
            }

            //
            if(!this.postInfo.description) {
                this.postInfo.description = this.postInfo.title;
            }
        },
        saveBlog: async function(published) {
            this.setPostInfo();
            this.postInfo.published = published;
            const {data: res} = await this.$axios.put("/admin/updateBlog",this.postInfo);
            if(res.status === 101) {
                this.$message.success(res.message);
                await this.$router.push("/listBlogs");
            } else {
                this.$message.error(",");
            }
        },
        publishBlog: async function() {
            await this.saveBlog(true);
        },
        //
        // @imgAdd event
        $imgAdd(pos, $file){
            // ..
            const formdata = new FormData();
            formdata.append('file', $file);
           this.$axios({
               url: '/admin/saveImages',
               method: 'post',
               data: formdata,
               headers: { 'Content-Type': 'multipart/form-data' },
           }).then((url) => {
               // .url![...](./0) -> ![...](url)
               // $vm.$img2Url 
               this.$refs.md.$img2Url(pos, url.data);
           })
        },
        uploadSuccess: function(response) {
            this.postInfo.firstPicture = response;
        }

    }
}
</script>



<style scoped>

.markdown {
    margin-top: 15px;
}

.rowDown {
    margin-top: 10px;
}

.textarea {
    margin-top: 15px;
}

.el-checkbox-group {
    margin-top: 15px;
}

.endbtns {
    margin-top: 15px;
    margin-bottom: 15px;
}

.tag_select {
    width: 300px;
}


</style>
