<template>
    <div>
        <el-row>
            <el-button class="add" type="primary" plain @click="handleAdd"></el-button>
        </el-row>
        <el-table :data="essays" border stripe>
            <el-table-column type="index"></el-table-column>
            <el-table-column label="" prop="content"></el-table-column>
            <el-table-column label="" width="300px">
                <template slot-scope="scope">
                    <i class="el-icon-time"></i>
                    <span style="margin-left: 10px">{{ dateFormat(scope.row.createTime) }}</span>
                </template>
            </el-table-column>
            <el-table-column label=""  width="150">
                <template slot-scope="scope">
                    <el-button size="mini" @click="handleEdit(scope.$index)"></el-button>
                    <el-button size="mini" type="danger" @click="handleDelete(scope.row.id)"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--  -->
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                       :current-page="queryInfo.pageNum" :page-sizes="[10, 12, 15, 20]" :page-size="queryInfo.pageSize"
                       layout="total, sizes, prev, pager, next, jumper" :total="total">
        </el-pagination>
        <el-dialog title=""  :visible.sync="dialogFormVisible">
            <el-input type="textarea" :rows="10" clearable autocomplete="off" placeholder="..." v-model="postInfo.content"></el-input>
            <div slot="footer" class="dialog-footer">
                <el-button @click="cancel"> </el-button>
                <el-button type="primary" @click="commit"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>

import dayjs from "dayjs";

export default {
    name: "ManageEssay",
    data() {
        return {
            essays: [],
            total: 0,
            dialogFormVisible: false,
            queryInfo: {
                pageNum: 1,
                pageSize: 10
            },
            postInfo: {
                id: 0,
                content: ""
            }
        }
    },
    methods: {
        async getEssayList() {
            const {data:res} = await this.$axios.get("/admin/essayList", {params: this.queryInfo});
            if(res.status !== 1) {
                this.$message.error("")
                return
            }
            this.essays.splice(0, this.essays.length)
            this.essays.push(...res.data)
            this.total = res.count
        },
        handleAdd() {
            this.dialogFormVisible = true
            this.postInfo.id = 0
        },
        handleEdit(index) {
            this.postInfo.content = this.essays[index].content
            this.postInfo.id = this.essays[index].id
            this.dialogFormVisible = true
        },
        async handleDelete(id) {
            this.$messageBox.confirm('?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(async () => {
                //
                const {data:res} = await this.$axios.delete("/admin/deleteEssay", {params: {id: id}});
                if (res.status !== 401) {
                    this.$message.error("")
                } else {
                    this.$message.success("")
                }
                //
                await this.getEssayList();
            }, () => {
                this.$message({type: 'info', message: ''});
            });
        },
        handleSizeChange: function(pagesize) {     // pagesize 
            this.queryInfo.pageSize = pagesize;
            this.getEssayList();
        },
        handleCurrentChange: function(newPage) {  // 
            this.queryInfo.pageNum = newPage;
            this.getEssayList();
        },
        dateFormat(d) {
            return dayjs(d).format("YYYY-MM-DD HH:mm:ss")
        },
        cancel() {
            this.dialogFormVisible = false
        },
        async commit() {
            let res
            if(this.postInfo.id === 0) {
                res = await this.$axios.post("/admin/addEssay", this.postInfo)
            } else {
                res = await this.$axios.put("/admin/updateEssay", this.postInfo)
            }
            if (res.data.status !== 101) {
                this.$message.error("")
            } else {
                this.cancel()
                this.postInfo.content = ""
                await this.getEssayList()
                this.$message.success("")
            }
        }
    },
    created() {
        this.getEssayList()
    }
}
</script>

<style scoped>

.add {
    float: right !important;
    margin-right: 50px;
}

</style>