
<template>

    <div>
        <!--  -->
        <el-card class="box-card">
            <!--  -->
            <el-row :gutter="15">
                <el-col :span="10">
                    <el-input v-model="inputType" @blur="checkTypeExist(false)" @focus="checkTypeExist(true)" placeholder="" clearable></el-input>
                </el-col>
                <el-col :span="4">
                    <el-button type="primary" @click="addType" :disabled="addBtnDisabled" icon="el-icon-edit">{{btnName}}</el-button>
                </el-col>
            </el-row>

            <!--  -->
            <el-table :data="types" border stripe>
                <el-table-column type="index"></el-table-column>
                <el-table-column label="" prop="name"></el-table-column>
               <el-table-column align="center" label=""  width="150">
                    <template slot-scope="scope">
                        <el-button size="mini" @click="handleEdit(scope.row.id, scope.row.name)"></el-button>
                        <el-button size="mini" type="danger" @click="handleDelete(scope.row.id)"></el-button>
                    </template>
               </el-table-column>
            </el-table>

            <!--  -->
             <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
            :current-page="queryInfo.pageNum" :page-sizes="[5, 8, 10, 12, 15]" :page-size="queryInfo.pageSize"
            layout="total, sizes, prev, pager, next, jumper" :total="types.total">
            </el-pagination>

        </el-card>


    </div>

</template>



<script>
export default {
    data() {
        return {
            btnName: "",
            inputType: "",
            inputTypeId: 0,
            types: [],
            addBtnDisabled: false,
            queryInfo: {
                pageNum: 1,
                pageSize: 5,
            }
        }
    },
    created() {
        this.getTypeList();   //
    },
    methods: {
        getTypeList: async function() {
            const{data: res} = await this.$axios.get("/admin/getPageTypes", {params: this.queryInfo})
            // console.log(res)
            if(res.status == 1) {
                this.types = res.data;
                this.types.total = res.total
            } else {
                this.$message.error("")
            }
        },
        checkTypeExist: async function(focused) {
            if(focused) {     //
                this.addBtnDisabled = false;
            }
            // 
            if(this.inputType === "") {
                return;
            }

            const{data: res} = await this.$axios.get("/admin/checkTypeExist", {params: {typeName:this.inputType} });
            //console.log(res)
            if(res.status == 1) {
                if(res.data == true) {
                    this.addBtnDisabled = true;
                } else {
                    this.addBtnDisabled = false;
                }
            }
        },
        handleEdit: function(id, name) {
            this.inputType = name;
            this.inputTypeId = id;
            this.btnName = "";
        },
        handleDelete: async function(typeId) {
            if(typeId <= 0) return;
            this.$messageBox.confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(async () => {
                const {data:res} = await this.$axios.delete("/admin/deleteType", {params: {id: typeId}});
                if(res.status === 401) {
                    this.$message.success(res.message);
                } else {
                    this.$message.error("");
                }
                this.getTypeList();
                //console.log("")
            }, () => {
                this.$message({type: 'info', message: ''});
            });
        },
        handleSizeChange: function(pageSize) {
            this.queryInfo.pageSize = pageSize;
            this.getTypeList();
        },
        handleCurrentChange: function(newPage) {
            this.queryInfo.pageNum = newPage;
            this.getTypeList();
        },
        addType: async function() {
            if (this.inputType === "") {
                this.$message.error("")
                return
            }
            if(this.btnName === "") {
                // Type name
                const {data:res} = await this.$axios.put("/admin/updateType", {id: this.inputTypeId, name: this.inputType});
                if(res.status === 101) {
                    this.$message.success(res.message);
                } else {
                    this.$message.error("")
                }
                this.inputType = "";
                this.inputTypeId = 0;
                this.btnName = "";
            } else {
                // type
                const {data:res} = await this.$axios.post("/admin/addType", {name: this.inputType});
                if(res.status === 101) {
                    this.$message.success(res.message);
                } else {
                    this.$message.error("")
                }
                this.inputType = "";
            }
            this.getTypeList();
        }
    }

}
</script>



