
<template>

    <div>
        <!--  -->
        <el-card class="box-card">
            <!--  -->
            <el-row :gutter="15">
                <el-col :span="8">
                    <el-input v-model="queryInfo.blogTitle" placeholder="" clearable></el-input>
                </el-col>

                <el-col :span="4">
                    <el-select :value="selectedType" @visible-change="getAllTypes" @change="changeType" clearable  placeholder="">
                        <el-option v-for="item in types" :key="item.id" :value="item.name">
                        </el-option>
                    </el-select>
                </el-col>

                <el-col :span="2">
                    <!--  -->
                    <el-checkbox v-model="queryInfo.recommended" label="" border></el-checkbox>
                </el-col>

                <el-col :span="4">
                    <el-button @click="clear" type="info">clear</el-button>
                    <el-button type="primary" @click="getBlogList" icon="el-icon-search"></el-button>
                </el-col>
            </el-row>

            <!--  -->
            <el-table :data="blogs" border stripe>
                <el-table-column type="index"></el-table-column>
                <el-table-column label="" prop="title"></el-table-column>
                <el-table-column label="" prop="typename" width="150"></el-table-column>
                <el-table-column label="" width="100">
                    <template slot-scope="scope">
                        {{scope.row.recommend ? '' : ''}}
                    </template>
                </el-table-column>
                <el-table-column label="" width="100">
                    <template slot-scope="scope">
                        {{scope.row.published ? '' : ''}}
                    </template>
                </el-table-column>
                <el-table-column label="" width="200">
                    <template slot-scope="scope">
                        <i class="el-icon-time"></i>
                        <span style="margin-left: 10px">{{ dateFormat(scope.row.createTime) }}</span>
                    </template>
                </el-table-column>
               <el-table-column label=""  width="150">
                    <template slot-scope="scope">
                        <el-button size="mini" @click="handleEdit(scope.row.id)"></el-button>
                        <el-button size="mini" type="danger" @click="handleDelete(scope.row.id)"></el-button>
                    </template>
                </el-table-column>
            </el-table>

            <!--  -->
             <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
            :current-page="queryInfo.pageNum" :page-sizes="[8, 10, 12, 15]" :page-size="queryInfo.pageSize"
            layout="total, sizes, prev, pager, next, jumper" :total="blogs.total">
            </el-pagination>
        </el-card>
    </div>

</template>



<script>
import dayjs from "dayjs";

export default {
    data() {
        return {
            types: [],
            selectedType: "",
            blogs: [],
            queryInfo: {          //
                pageNum: 1,
                pageSize: 8,
                blogTitle: "",
                typeId: 0,
                recommended: false,
            }
        }
    },
    created() {
        this.getBlogList();   //
    },
    methods: {
        clear: function() {
            this.queryInfo.blogTitle = "";
            this.queryInfo.typeId = 0;
            this.queryInfo.recommended = false;
            this.selectedType = "";
        },
        getBlogList: async function() {
            if(this.queryInfo.blogTitle === "") {
                this.queryInfo.blogTitle = null;
            }
            if(this.queryInfo.typeId === 0) {
                this.queryInfo.typeId = null;
            }
            if(this.queryInfo.recommended === false) {
                this.queryInfo.recommended = null;
            }

            const{data: res} = await this.$axios.get("/admin/searchBlogs",{params: this.queryInfo});
            if(res.status === 1) {
                this.blogs = [...res.data];
                this.blogs.total = res.count
            } else {
                this.$message.error(",");
            }

        },

        getAllTypes: async function(change) {
            if(change === true) {
                const{data: res} = await this.$axios.get("/admin/getAllTypes");
                if(res.status === 1) {
                    this.types = res.data;
                } else {
                    this.$message.error(res.message);
                }
            }
        },
        changeType: function(name) {
            this.selectedType = name;
            const item = this.types.find(item => {
                return item.name === name
            });
            this.queryInfo.typeId = item.id;
        },
        handleEdit: function(id) {
            this.$router.push({
                path: "addBlog",
                query: {
                    id: id
                }
            });
        },
        handleDelete: async function(id) {
            this.$messageBox.confirm('?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(async () => {
                //
                const{data: res} = await this.$axios.delete("/admin/deleteBlog", {params: {id:id}});
                if(res.status === 401) {
                    this.$message.success("");
                } else {
                    this.$message.error("");
                }
                //
                await this.getBlogList();
            }, () => {
                this.$message({type: 'info', message: ''});
            });

        },
        handleSizeChange: function(pagesize) {     // pagesize 
            this.queryInfo.pageSize = pagesize;
            this.getBlogList();
        },
        handleCurrentChange: function(newPage) {  // 
            this.queryInfo.pageNum = newPage;
            this.getBlogList();
        },
        dateFormat(d) {
            return dayjs(d).format("YYYY-MM-DD HH:mm:ss")
        }
    }
}
</script>



<style lang="less" scoped>



</style>
