<template>
    <div class="panel" id="rp">
        <div class="wrapper">
            <div class="info">
                <input type="text" placeholder="" v-model="postInfo.nickname">
                <input type="text" placeholder=" ()" v-model="postInfo.email">
                <input type="text" placeholder=" (, httphttps)" v-model="postInfo.url">
            </div>
            <textarea name="" id="" cols="30" rows="10" v-model="postInfo.content" placeholder="..."></textarea>
            <div class="btn-area">
                <button @click="commit"></button>
                <button @click="clearAll"></button>
                <button v-if="closeBtn" @click="close"></button>
            </div>
        </div>
    </div>

</template>

<script>
export default {
    name: "LeaveMessagePanel",
    props: ["closeBtn", "msgInfo"],
    watch: {
        msgInfo: {
            handler(newVal) {
                this.postInfo.parentId = newVal.parentId
                this.postInfo.topParentId = newVal.topParentId
            },
            deep: true
        }
    },
    data() {
        return {
            postInfo: {
                nickname: "",
                email: "",
                url: "",
                content: "",
                parentId: 0,
                topParentId: 0
            },
            commented: false,
            repeatCommitTimes: 0
        }
    },
    methods: {
        async commit() {
            if(!this.validate()) {
                return
            }

            this.trimSpace()
            const {data: res} = await this.$axios.post("/myblog/leaveMsg", this.postInfo);
            if(res.status === 101) {
                this.$message.success("")
                this.postInfo.content = ""
                if(this.closeBtn) {
                    this.close()
                }
            } else {
                this.commented = false
                this.$message.success("")
            }
        },
        validate() {
            // >3
            if(this.commented) {
                this.$message.error("")
                this.repeatCommitTimes++
                if(this.repeatCommitTimes > 3) {
                    this.clearAll()
                }
                return false
            }

            // 
            if(this.postInfo.nickname.trim() === "") {
                this.$message.error("")
                return false
            } else if(this.postInfo.nickname.length > 28) {
                this.$message.error("28")
                return
            }
            // 
            const t = /^[A-Za-zd0-9]+([-_.][A-Za-zd]+)*@([A-Za-zd]+[-.])+[A-Za-zd]{2,5}$/;
            if(!t.test(this.postInfo.email)) {
                this.$message.error("")
                this.postInfo.email = ""
                return false
            }
            // 
            if(this.postInfo.url !== "") {
                const reg = /(http|ftp|https):\/\/[\w\-_]+(\.[\w\-_]+)+([\w\-\.,@?^=%&amp;:/~\+#]*[\w\-\@?^=%&amp;/~\+#])?/;
                if(!reg.test(this.postInfo.url)) {
                    this.$message.error("")
                    this.postInfo.url = ""
                    return false
                }
            }

            // 
            if(this.postInfo.content == "") {
                this.$message.error("")
                return false
            } else if(this.postInfo.content.length > 1500) {
                this.$message.error("1500")
                return false
            }

            this.commented = true
            this.repeatCommitTimes = 0
            setTimeout(() => {
                this.commented = false
            }, 60 * 1000)

            return true
        },
        clearAll() {
            this.postInfo = {
                nickname: "",
                email: "",
                url: "",
                content: ""
            }
        },
        trimSpace() {
            this.postInfo.nickname.trim()
            this.postInfo.email.trim()
            this.postInfo.content.trim()
            this.postInfo.url.trim()
        },
        close() {
            this.$bus.$emit("hideReplay")
        }
    }
}
</script>

<style scoped>

.panel {
    border-radius: 8px;
    box-shadow: 0 15px 35px rgb(50 50 93 / 18%), 0 5px 15px rgb(0 0 0 / 18%) !important;
    background-color: #fff;
    width: 1070px;
}

.wrapper {
    padding: 10px 10px 15px;
}

input {
    width: 350px;
    height: 66px;
    outline: none;
    border: none;
    border-bottom: 1px dashed #dedede;
}

input:focus {
    border-bottom: 1px dashed #e4933c;
    box-shadow: 0 1px 0 0 #def605;
    transition: all 0.5s ease;
}



.info {
    margin-bottom: 10px;
}

textarea {
    outline: none;
    resize: none;
    width: 100%;
    border: none;
    border-bottom: 1px dashed #dedede;
    background: url("../assets/images/comment_bg.webp") no-repeat top right;
}

.btn-area {
    overflow: hidden;
    padding-top: 10px;
    padding-bottom: 10px;
}

button {
    float: right;
    border: 1px solid rgb(88 80 236);
    background-color: rgb(240 245 255);
    font-size: .875rem;
    padding: 0.625rem 1.25rem;
    border-radius: 0.25rem;
    margin-right: 20px;
}

button:active {
    transform: translateY(2px);
}
</style>