<template>
    <div>
        <el-card>
            <el-row>
                <el-button class="add" type="primary" plain @click="handleAdd"></el-button>
                <el-button class="show-categories" type="primary" plain @click="showCategories"></el-button>
            </el-row>
            <!--  -->
            <el-table :data="links" border stripe>
                <el-table-column type="index"></el-table-column>
                <el-table-column label="" prop="name" width="300px"></el-table-column>
                <el-table-column label="" prop="desc"></el-table-column>
                <el-table-column label="" prop="category" width="180px"></el-table-column>
                <el-table-column label="" prop="url"></el-table-column>
                <el-table-column label=""  width="150">
                    <template slot-scope="scope">
                        <el-button size="mini" @click="handleEdit(scope.$index)"></el-button>
                        <el-button size="mini" type="danger" @click="handleDelete(scope.row.id)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!--  -->
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                           :current-page="queryInfo.pageNum" :page-sizes="[10, 12, 15, 20]" :page-size="queryInfo.pageSize"
                           layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
            <el-dialog title="" :visible.sync="dialogFormVisible">
                <el-form label-width="80px">
                    <el-form-item label="">
                        <el-input v-model="postInfo.name" autocomplete="off" clearable></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="postInfo.url" autocomplete="off" clearable></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-select :value="selectedCategory" @visible-change="getAllCategories" @change="changeCategory" clearable placeholder="">
                            <el-option v-for="item in categories" :key="item.id" :value="item.name">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input type="textarea" :rows="3" v-model="postInfo.desc" autocomplete="off" clearable></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-row :gutter="22">
                            <el-col :span="20">
                                <el-input v-model="postInfo.icon" autocomplete="off" clearable></el-input>
                            </el-col>
                            <el-col :span="2">
                                <el-upload :on-success="uploadSuccess" :show-file-list="false" class="upload-demo" :action="uploadIcon">
                                    <el-button  type="primary" plain></el-button>
                                </el-upload>
                            </el-col>
                        </el-row>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="cancel"> </el-button>
                    <el-button type="primary" @click="commitLink"> </el-button>
                </div>
            </el-dialog>
            <el-dialog title="" :visible.sync="dialogCategoryVisible">
                <el-row>
                    <el-button class="add" type="primary" plain @click="innerVisible=true;categoryPost.id=0"></el-button>
                </el-row>
                <el-table :data="categories" border stripe>
                    <el-table-column type="index"></el-table-column>
                    <el-table-column label="" prop="name"></el-table-column>
                    <el-table-column label=""  width="150">
                        <template slot-scope="scope">
                            <el-button size="mini" @click="editCategory(scope.$index)"></el-button>
                            <el-button size="mini" type="danger" @click="deleteCategory(scope.row.id)"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <el-dialog
                    width="40%"
                    title=""
                    :visible.sync="innerVisible"
                    append-to-body>
                    <el-input placeholder="" v-model="categoryPost.name"></el-input>
                    <div slot="footer" class="dialog-footer">
                        <el-button @click="addCategoryCancel"> </el-button>
                        <el-button type="primary" @click="commitCategory"> </el-button>
                    </div>
                </el-dialog>
            </el-dialog>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogCategoryVisible=false"> </el-button>
            </div>
        </el-card>
    </div>
</template>

<script>
import axios from "axios";

export default {
    name: "manageResLink",
    data() {
        return {
            links: [],
            dialogFormVisible: false,
            dialogCategoryVisible: false,
            innerVisible: false,
            total: 0,
            selectedCategory: "",
            postInfo: {
                id: 0,
                name: "",
                desc: "",
                url: "",
                categoryId: 0,
                icon: "",
            },
            categoryPost: {
                id: 0,
                name: ""
            },
            queryInfo: {
                pageNum: 1,
                pageSize: 10
            },
            categories: [],
            uploadIcon: axios.defaults.baseURL + "/admin/uploadIcon"
        }
    },
    methods: {
        async getLinkList() {
            const {data:res} = await this.$axios.get("/admin/pageLinks", {params: this.queryInfo});
            console.log(res)
            if(res.status !== 1) {
                this.$message.error("")
                return
            }
            if(!res.data || !res.categories) {
                return
            }
            this.total = res.count
            this.categories = res.categories
            const m = new Map()
            this.links.splice(0, this.links.length)
            res.categories.forEach((val => {
                m.set(val.id, val.name)
            }))
            res.data.forEach((val) => {
                this.links.push({...val, category: m.get(val.categoryId)})
            })
        },
        async getAllCategories() {
            const {data:res} = await this.$axios.get("/admin/categories");
            if (res.status !== 1) {
                this.$message.error("")
                return
            }
            this.categories = res.data
        },
        changeCategory(name) {
            console.log(name)
            this.selectedCategory = name
            const val = this.categories.find(item => {
                return item.name === name
            })
            this.postInfo.categoryId = val.id
            console.log(this.postInfo)
        },
        async handleAdd() {
            this.postInfo.id = 0
            this.dialogFormVisible = true
        },
        async handleEdit(index) {
            this.dialogFormVisible = true
            this.postInfo = {...this.links[index]}
            const val = this.categories.find(item => {
                return item.id === this.links[index].categoryId
            })
            this.selectedCategory = val.name
        },
        async handleDelete(id) {
            this.$messageBox.confirm('?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(async () => {
                //
                const {data:res} = await this.$axios.delete("/admin/deleteLink", {params: {id: id}});
                if (res.status !== 401) {
                    this.$message.error("")
                } else {
                    this.$message.success("")
                }
                //
                await this.getLinkList();
            }, () => {
                this.$message({type: 'info', message: ''});
            });
        },
        handleSizeChange: function(pagesize) {     // pagesize 
            this.queryInfo.pageSize = pagesize;
            this.getLinkList();
        },
        handleCurrentChange: function(newPage) {  // 
            this.queryInfo.pageNum = newPage;
            this.getLinkList();
        },
        cancel() {
            this.postInfo = {
                id: 0,
                name: "",
                desc: "",
                url: "",
                categoryId: 0,
                icon: "",
            }
            this.dialogFormVisible = false
            this.selectedCategory = ""
        },
        async commitLink() {
            let res
            if(this.postInfo.id === 0) {
                res = await this.$axios.post("/admin/addLink", this.postInfo)
            } else {
                res = await this.$axios.put("/admin/updateLink", this.postInfo)
            }
            if (res.data.status !== 101) {
                this.$message.error("")
            } else {
                this.cancel()
                await this.getLinkList()
                this.$message.success("")
            }
        },
        uploadSuccess(response) {
            this.postInfo.icon = response;
        },
        showCategories() {
            this.dialogCategoryVisible = true
        },
        addCategoryCancel() {
            this.categoryPost.name = ''
            this.categoryPost.id = 0
            this.innerVisible = false
        },
        async commitCategory() {
            let res
            if(this.categoryPost.id === 0) {
                res = await this.$axios.post("/admin/addCategory", this.categoryPost);
            } else {
                res = await this.$axios.put("/admin/updateCategory", this.categoryPost)
            }

            if (res.data.status === 101) {
                this.$message.success("")
                this.addCategoryCancel()
                await this.getAllCategories()
            } else {
                this.$message.error("")
            }
        },
        editCategory(index) {
            this.categoryPost.name = this.categories[index].name
            this.categoryPost.id = this.categories[index].id
            this.innerVisible = true
        },
        async deleteCategory(id) {
            this.$messageBox.confirm('?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(async () => {
                const {data:res} = await this.$axios.delete("/admin/deleteCategory", {params: {id: id}});
                if (res.status === 401) {
                    this.$message.success("")
                } else {
                    this.$message.error("")
                }
                //
                await this.getAllCategories();
            }, () => {
                this.$message({type: 'info', message: ''});
            });

        }
    },
    created() {
        this.getLinkList()
    }
}
</script>

<style scoped>

.add {
    float: right !important;
    margin-right: 50px;
}

.show-categories {
    float: right;
    margin-right: 30px;
}

</style>