
<template>

    <el-container class="home_container">
            <!--  -->
        <el-header>
            <div>
                <span></span>
            </div>
            <el-button type="info" @click="logout"></el-button>
        </el-header>

        <el-container>
            <!--  -->
            <el-aside :width="isCollapse ? '65px' : '200px'">
                <div class="toggle-button" @click="toggleCollapse">|||</div>
                <el-menu background-color="#333744" text-color="#fff" active-text-color="#409eff"
                unique-opened :collapse="isCollapse" :collapse-transition="false" router
                :default-active="activePath">

                    <el-submenu :index="menu.id" v-for="(menu, i1) in menus" :key="menu.id">
                        <!--  -->
                        <template slot="title">
                            <!--  -->
                            <i :class="menu.icon"></i>
                            <!--  -->
                            <span>{{ menu.name }}</span>
                        </template>

                        <!--  -->
                        <el-menu-item :key="subMenu.id" v-for="(subMenu, i2) in menu.submenus" :index="subMenu.index" @click="saveNavState(i1, i2)">
                            <template slot="title">
                                <!--  -->
                                <i :class="subMenu.icon"></i>
                                <!--  -->
                                <span>{{ subMenu.name }}</span>
                            </template>
                        </el-menu-item>
                    </el-submenu>
                </el-menu>-->
            </el-aside>

            <!--  -->
            <el-main>
                <el-breadcrumb separator-class="el-icon-arrow-right">
                    <el-breadcrumb-item>{{ breadcrumbItem1 }}</el-breadcrumb-item>
                    <el-breadcrumb-item>{{ breadcrumbItem2 }}</el-breadcrumb-item>
                </el-breadcrumb>
                <!--  -->
                <router-view></router-view>
            </el-main>
        </el-container>
    </el-container>


</template>



<script>
export default {
    data() {
        return {
            isCollapse: false,
            activeMenu: -1,
            activeSubMenu: -1,
            menus: [
                {
                    id :"1",
                    icon: "el-icon-edit",
                    name: "",
                    submenus: [
                        {id:"11", index: "/listBlogs", icon:"el-icon-menu", name:""},
                        {id:"12", index: "/addBlog", icon:"el-icon-plus", name:""},
                        {id:"13", index: "/listMottos", icon:"el-icon-menu", name:""}
                    ]
                },
                {
                    id :"2",
                    icon: "el-icon-notebook-2",
                    name: "",
                    submenus: [
                        {id:"21", index: "/listTypes", icon:"el-icon-menu", name:""},
                    ]
                },
                {
                    id :"3",
                    icon: "el-icon-paperclip",
                    name: "",
                    submenus: [
                        {id:"31", index: "/listTags", icon:"el-icon-menu", name:""},
                    ]
                },
                // {
                //     id :"4",
                //     icon: "el-icon-picture-outline",
                //     name: "",
                //     submenus: [
                //         {id:"41", index: "/listImages", icon:"el-icon-menu", name:""},
                //     ]
                // },
                {
                    id :"5",
                    icon: "el-icon-notebook-1",
                    name: "",
                    submenus: [
                        {id:"51", index: "/manageEssay", icon:"el-icon-menu", name:""},
                    ]
                },
                {
                    id :"6",
                    icon: "el-icon-chat-line-square",
                    name: "",
                    submenus: [
                        {id:"61", index: "/manageMsg", icon:"el-icon-menu", name:""},
                    ]
                },
                {
                    id :"7",
                    icon: "el-icon-star-off",
                    name: "",
                    submenus: [
                        {id:"71", index: "/manageLink", icon:"el-icon-menu", name:""},
                    ]
                }
            ]
        }
    },
    methods: {
        logout() {
            // token
            window.sessionStorage.clear();
            // 
            this.$router.push("/manage/login");
        },
        toggleCollapse() {   //
            this.isCollapse = !this.isCollapse;
        },
        saveNavState(i1, i2) {     //
            this.activeMenu = i1
            this.activeSubMenu = i2
        }
    },
    computed: {
        activePath() {
            return this.activeMenu === -1 || this.activeSubMenu === -1 ?
                null : this.menus[this.activeMenu].submenus[this.activeSubMenu].index
        },
        breadcrumbItem1() {
            return this.activeMenu === -1 ? null : this.menus[this.activeMenu].name
        },
        breadcrumbItem2() {
            return this.activeMenu === -1 || this.activeSubMenu === -1 ?
                null : this.menus[this.activeMenu].submenus[this.activeSubMenu].name
        }
    }
}
</script>



<style lang="less" scoped>
.home_container {
    height: 100%;
}

.el-header {
    background-color: #373D41;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #fff;
    > div {
        display: flex;
        align-items: center;
        span {
            margin-left: 100px;
            font-size: 30px;
            font-weight: bold;
        }
    }
}

.el-aside {
    background-color: #333744;
    ele-menu {
        border-right: none;
    }
}

.el-main {
   background-color: #fff;
}

.el-menu {
    background-color: #1B1C1D;
    opacity: 1;

    font-weight: 500;
    font-size: 50px;
}

.toggle-button {
    background-color: #4A5064;
    font-size: 10px;
    line-height: 24px;
    color: #fff;
    text-align: center;
    letter-spacing: 0.2em;
    cursor: pointer;
}
</style>
