import '@babel/polyfill'
import 'mutationobserver-shim'
import Vue from 'vue'
import './plugins/bootstrap-vue'
import App from './App.vue'
import router from './router'
import'./plugins/element.js'
import 'element-ui/lib/theme-chalk/index.css'
import 'element-ui/lib/theme-chalk/display.css'
import './assets/css/global.css'   //
import './plugins/echarts-vue.js'

import  mavonEditor from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'

import axios from "axios"
import "./plugins/common"

import 'highlight.js/styles/googlecode.css' //
import 'highlight.js/styles/a11y-dark.css'

import {bubbles} from 'vue-canvas-effect';


Vue.component('bubbles-effect', bubbles);

Vue.use(mavonEditor)


//axios.defaults.baseURL="http://47.99.200.62/api"
axios.defaults.baseURL="http://192.168.44.100/api"

//token
axios.interceptors.request.use(config =>{
  let requestUrl = config.url
  let start = requestUrl.indexOf("/admin")
  if (start == 0) {   // token
    config.headers.Authorization = window.sessionStorage.getItem("token")
  }

  return config           //return config
})

// 
axios.interceptors.response.use(config => {
  if(config.status == 500) {
    router.push("/internalError");
  } else if(config.status == 404) {
    router.push("/notFound");
  }

  if(config.data.status == 300) {    // 300
    router.push("/manage/login");
  }

  return config;
})

Vue.prototype.$axios = axios

Vue.config.productionTip = false



new Vue({
  router,
  render: h => h(App),
  beforeCreate() {
    Vue.prototype.$bus = this   // 
  }
}).$mount('#app')
