
<template>

    <div>
        <!---->
        <div id="bg1" :style="bgObj" >
            <bubbles-effect :options="options" class="bubble"></bubbles-effect>
            <div id="motto" v-if="showMotto">
                <transition-group appear
                name="animate__animated animate__bounce animate__slow"
                enter-active-class="animate__bounceIn"
                leave-active-class="animate__bounceOut"
                >
                    <h1 key="1">{{firstBGPageInfo.curMotto.ch}}</h1>
                    <p key="2">{{firstBGPageInfo.curMotto.en}}</p>
                </transition-group>
            </div>
            <div class="scroll-down">
                <a class="anchor-down" @click.prevent="anchorDown"></a>
            </div>
        </div>



        <!---->
        <div id="area-blow">
            <div class="blog-area">

                <!---->
                <div class="blog-left">
                    <!---->
                    <UserInfoCard :count="blogCount"></UserInfoCard>
                    <!---->
                    <RecommendList :blogList="newRecommend"></RecommendList>
                    <!---->
                    <TagCloud></TagCloud>
                    <!---->
                    <RecommendList :blogList="hotBlogs"></RecommendList>
                </div>

                <!---->
                <div class="blog-right">
                    <BlogCard :key="item.id" v-for="(item, index) in blogDetails"
                               :item="item" :imgRight="index % 2 === 0"
                                @click.native="blogDetail(item.id)">
                    </BlogCard>
                    <!---->
                    <Pagination @jumpPage="jumpPage" :pageInfo="{pageNum:queryInfo.pageNum, pages:pages}"></Pagination>

                </div>
            </div>
        </div>
    </div>

</template>



<script>
import img1 from "../assets/1.png"
import img2 from "../assets/2.jpg"
import img3 from "../assets/3.jpg"

import BackToTop from "../components/BackToTop";
import BlogCard from "../components/BlogCard";
import UserInfoCard from "../components/UserInfoCard";
import Pagination from "../components/Pagination";
import RecommendList from "../components/RecommendList";
import TagCloud from "../components/TagCloud";

import "animate.css"
// import "../assets/js/ribbons"

export default {
    name: "Home",
    components: {BackToTop, BlogCard, Pagination, UserInfoCard, RecommendList, TagCloud},
    data() {
        return {
            options: {
                color: 'random', //Bubble color
                radius: 15, //Bubble radius
                densety: 0.2, // The larger the bubble density, the greater the density (suggest no more than 1).
                clearOffset: 0.2 // The larger the bubble disappears [0-1], the longer it disappears.
            },
            firstBGPageInfo: {
                curBg: "",
                bgs: [],
                curMotto: {
                    ch: ", !",
                    en: "Struggle never stops, and progress never ends!"
                },
                mottos:[],
            },
            showCat: false,
            showMotto: true,
            pages: 1,              // 
            blogCount: 0,
            queryInfo: {
              pageNum: 1,
              pageSize: 8
            },
            blogDetails: [{
              id: 1,
              title: "",
              description: "...",
              firstPicture: img1,
              createTime: "2021-03-29",
              views: 1024,
              nickname: "Jack Ma",
              typename: ""
              },
              {
                id: 2,
                title: "",
                description: "...",
                firstPicture: img2,
                createTime: "2021-03-29",
                views: 1024,
                nickname: "Jack Ma",
                typename: ""
              },
              {
                id: 3,
                title: "",
                description: "...",
                firstPicture: img3,
                createTime: "2021-03-29",
                views: 1024,
                nickname: "Jack Ma",
                typename: ""
              }],
            newRecommend: {
                title: "",
                icon: "icon-zuixingengxin",
                list: [
                    {id: 1, title: "MarkDownit"},
                    {id: 2, title: "Vue"},
                    {id: 3, title: ""},
                    {id: 4, title: "MavonEditor"},
                    {id: 5, title: "SpringbootPageHelper "},
                    {id: 6, title: "mybatis"},
                    {id: 7, title: "maven"}
                ]
            },
            hotBlogs: {
                title: "",
                icon: "icon-fire",
                list: [
                    {id: 1, title: "MarkDownit"},
                    {id: 2, title: "Vue"},
                    {id: 3, title: ""},
                    {id: 4, title: "MavonEditor"},
                    {id: 5, title: "SpringbootPageHelper "},
                    {id: 6, title: "mybatis"},
                    {id: 7, title: "maven"}
                ]
            },

        }
    },
    created() {
        this.getBlogLists();
        this.getBackgImage()
        this.getMotto()
        this.getNewBlogs()
        this.getHotBlogs()
    },
    deactivated() {
        this.showMotto = false
    },
    activated() {
        this.showMotto = true
        window.scrollTo(0, 0)
    },
    computed: {
        bgObj() {
            if (this.firstBGPageInfo.curBg === "") {
                return ""
            }
            return {backgroundImage: 'url(' + this.firstBGPageInfo.curBg + ')'}
        }
    },
    methods: {
        // 
        async getBackgImage() {
            const {data: res} = await this.$axios.get("/myblog/bgs")
            if(res.status === 1) { //
                this.firstBGPageInfo.bgs = res.data
                const n = Math.round(Math.random() * (res.data.length - 1));
                this.firstBGPageInfo.curBg = res.data[n]
            }
        },
        // 
        async getMotto() {
            const {data: res} = await this.$axios.get("/myblog/mottos")
            if(res.status === 1) {
                this.firstBGPageInfo.mottos = res.data
                const n = Math.round(Math.random() * (res.data.length - 1));
                this.firstBGPageInfo.curMotto = res.data[n]
            }
        },
        anchorDown() {
            const offsetTop = document.getElementById("area-blow").offsetTop;
            window.scrollTo({top: offsetTop, behavior: 'smooth'})
        },
        // 
        async getBlogLists() {
            var keyWord = window.sessionStorage.getItem("keyWord");
            if(keyWord) {
                window.sessionStorage.removeItem("keyWord");
                const {data:res} = await this.$axios.get("/myblog/search", {params: {pageNum: this.queryInfo.pageNum, pageSize: this.queryInfo.pageSize, keyWord: keyWord}});
                if(res.status === 1) {
                    this.blogDetails = res.data;
                    this.blogCount = res.count
                    this.pages = Math.ceil(res.count / this.queryInfo.pageSize)
                    if (this.pages <= 0) {
                        this.pages = 1
                    }
                }else {
                    this.$message.error("")
                }
            } else {
                const {data:res} = await this.$axios.get("/myblog/blogLists", {params: this.queryInfo});
                if(res.status === 1) {
                    this.blogDetails = res.data;
                    this.blogCount = res.count
                    this.pages = Math.ceil(res.count / this.queryInfo.pageSize)
                    if (this.pages <= 0) {
                        this.pages = 1
                    }
                }else {
                    this.$message.error("")
                }
            }
        },
        // 
        async getNewBlogs() {
            const {data:res} = await this.$axios.get("/myblog/newBlogs", {params: {countLimit: 10}});
            if(res.status === 1) {
                this.newRecommend.list = res.data;
                console.log(res)
            } else {
                this.$message.warning("")
            }
        },
        // 
        async getHotBlogs() {
            const {data:res} = await this.$axios.get("/myblog/hotBlogs", {params: {countLimit: 10}});
            if(res.status === 1) {
                this.hotBlogs.list = res.data;
            } else {
                this.$message.warning("")
            }
        },
        // 
        blogDetail(id) {
            this.$router.push({
                path: "/blogDetail",
                query: {
                    id: id
                }
            });
        },
        jumpPage(pageNum) {
            this.queryInfo.pageNum = pageNum;
            this.getBlogLists();
        }
    }
}
</script>



<style lang="less" scoped>

.animate__animated{
    animation-duration: 3s !important;
}

.bubble {
    width: 100%;
}

//
#motto {
    user-select: none;
    font-weight: 560;
    line-height: 1.25;
    color: white;
    text-align: center;
    position: relative;
    top: 42%;
    h1 {
        padding-bottom: 20px;
    }

    p {
        font-size: 22px;
    }
}


.scroll-down {
    width: 100%;
    height: 60px;
    position: relative;
    top: 75%;
    text-align: center;

    animation: bounce-in 5s 1s infinite;
}

.anchor-down {
    display: block;
    width: 36px;
    height: 36px;
    transform: rotate(45deg);
    position: absolute;
    left: 50%;
    margin-left: -18px;
    cursor: pointer;
}

.anchor-down::before {
    content: '';
    width: 28px;
    height: 28px;
    position: absolute;
    right: 14px;
    bottom: 14px;
    border-right: 5px solid #fccb90;
    border-bottom: 5px solid #fccb90;
    display: block;
    border-bottom-right-radius: 2px;
}

.anchor-down::after {
    content: '';
    width: 28px;
    height: 28px;
    position: absolute;
    right: 0;
    bottom: 0;
    border-right: 5px solid #84fab0;
    border-bottom: 5px solid #84fab0;
    display: block;
    border-bottom-right-radius: 2px;
}

@keyframes bounce-in {
    0% { transform: translateY(0); }
    20% { transform: translateY(0); }
    50% { transform: translateY(-20px);  border-right: #81f5ac; border-top:#81f5ac;}
    80% { transform: translateY(0); }
    100% { transform: translateY(0); }
}

//
#bg1{
    width: 100%;
    height: 100vh;
    background: url('~@/assets/images/back4.png') 0 0 / cover no-repeat;
    overflow: hidden;
}

//
#area-blow {
    //background: url('~@/assets/images/back3.jpg') 0 0 / cover no-repeat;
    background-image: linear-gradient(to left, rgba(255, 0, 149, 0.2), rgba(0, 247, 255, 0.2));
    background-attachment: fixed;
}

// 
.blog-area {
    width: 1165px;
    margin: 0 auto;
    padding-top: 36px;
    padding-bottom: 64px;
    overflow: hidden;
}

.card {
    height: 260px;
    position: relative;
    background: #FFF;
    box-shadow: 0 1px 2px 0 rgba(34,36,38,.15);
    margin: 1rem 0;
    padding: 0px 24px !important;
    border-radius: .28571429rem;
    border: 1px solid rgba(34,36,38,.15);
    //opacity: 0.9;
    padding: 1.5em;
    font-size: 1rem;
    img {
        width: 360px !important;
    }
}

.blog-right {
    float: left;
}

.blog-left {
    float: left;
    width: 300px;
    margin-right: 15px;
    padding: 0 15px;
    border-radius: 6px;
    transition: all 0.3s;
}


</style>
